import React, { useState } from "react";
import { FaUser, FaLock,FaEnvelope } from "react-icons/fa";
import { Form, InputContainer, Title, Button } from "../styled-components/sign";

const SignUpForm = ({signUp}) => {

  const [values, setValues] = useState({
    name: "",
    email: "",
    password: "",
    error: "",
    success: false
  });

const handleChange = (name) => e => {
  setValues({...values, [name]: e.target.value, error: false })
}

const handleSubmit = (e) => {
  e.preventDefault();
  console.log(process.env)
}

 console.log(values);
  return (
    <Form onSubmit={handleSubmit} visible={signUp}>
      <Title>Sign up</Title>
      <InputContainer>
        <FaUser />
        <input onChange={handleChange("name")} type="text" placeholder="Username" />
      </InputContainer>
      <InputContainer>
        <FaEnvelope/>
        <input onChange={handleChange("email")} type="email" placeholder="Email" />
      </InputContainer>
      <InputContainer>
        <FaLock />
        <input onChange={handleChange("password")} type="password" placeholder="Password" />
      </InputContainer>
      <Button>Login</Button>
    </Form>
  );
};
export default SignUpForm;
